import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  template: `
  <h1 [ngClass]="titleClasses"> Hello There!</h1>

  <app-my-new-component> </app-my-new-component>

  <app-my-animations> </app-my-animations>
  `,
  styles: [`
.red-title {
   color:red;
}
.large-title {
    font-size:4em;
`]
})

export class AppComponent {
  titleClasses = { 'red-title': true, 'large-title': true }
}



